import { AllUsersChartsData } from "../../../../../models/Stats";
import { Chart, ChartSection, ChartsTab, useCharts } from "../../components/Charts";



export default function UsersCharts() {
    return (
        <ChartsTab>
            <ActualLoader />
        </ChartsTab>
    );
}


function ActualLoader() {
    const { chartsData } = useCharts<AllUsersChartsData>();

    return (
        <ChartSection defaultOpen title='Statystyki uytkownikw'>
            <Chart
                fullWidth={chartsData.users.length >= 6}
                title='Ilo rezerwacji na uytkownika'
                data={chartsData.users}
                xKey='email'
                y={[
                    { label: 'Ilo rezerwacji', key: 'reservationsCount' }
                ]}
            />
            <Chart
                fullWidth={chartsData.users.length >= 6}
                title="Czas wszystkich reserwacji w dany dzie"
                data={chartsData.users}
                xKey='email'
                y={[
                    { label: 'Czas cakowity', key: 'allTimeMinutes' },
                ]}
            />
            <Chart
                fullWidth
                title="rednie czasy rezerwacji"
                data={chartsData.users}
                xKey='email'
                y={[
                    { label: 'redni planowany czas', key: 'avgPlannedTimeMinutes' },
                    { label: 'redni faktyczny czas', key: 'avgActualTimeMinutes' },
                ]}
            />
        </ChartSection>
    );
}