import { BuildingsOrRoomsChartsData } from "../../../../../models/Stats";
import { Chart, ChartSection, ChartsTab, useCharts } from "../../components/Charts";



export default function AddressCharts() {
    return (
        <ChartsTab>
            <ActualLoader />
        </ChartsTab>
    );
}

function ActualLoader() {
    const { chartsData } = useCharts<BuildingsOrRoomsChartsData>();

    return (
        <ChartSection defaultOpen title='Statystyki budynkw'>
            <Chart
                fullWidth
                title='Ilo rezerwacji'
                data={chartsData.allReservations}
                xKey='name'
                y={[
                    { label: 'Ilo rezerwacji', key: 'reservationsCount' }
                ]}
            />
            <Chart
                fullWidth
                title="Czas wszystkich reserwacji"
                data={chartsData.allReservations}
                xKey='name'
                y={[
                    { label: 'Czas cakowity', key: 'allTimeMinutes' },
                ]}
            />
            <Chart
                fullWidth
                title="rednie czasy rezerwacji"
                data={chartsData.allReservations}
                xKey='name'
                y={[
                    { label: 'redni planowany czas', key: 'avgPlannedTimeMinutes' },
                    { label: 'redni faktyczny czas', key: 'avgActualTimeMinutes' },
                ]}
            />
        </ChartSection>
    );
};