import { BuildingOrRoomChartsData } from "../../../../models/Stats";
import { ChartsTab, Chart, ChartSection, useCharts } from "./Charts";
import MonthlyChartsSection from "./MonthlyChartsSection";
import WeeklyChartsSection from "./WeeklyChartsSection";



export default function RoomOrBuildingChartTab() {
    return (
        <ChartsTab>
            <ActualLoader />
        </ChartsTab>
    );
}

// need to use actual loading component to access charts context in ChartsTab
function ActualLoader() {
    const { chartsData } = useCharts<BuildingOrRoomChartsData>();

    return (
        <>
            <WeeklyChartsSection data={chartsData.weekly} />

            <MonthlyChartsSection data={chartsData.monthly} />

            <ChartSection defaultOpen title='Wykresy odnonie uytkownikw'>
                <Chart
                    fullWidth
                    title="rednie czasy rezerwacji"
                    data={chartsData.users}
                    xKey='email'
                    y={[
                        { label: 'redni planowany czas', key: 'avgPlannedTimeMinutes' },
                        { label: 'redni faktyczny czas', key: 'avgActualTimeMinutes' },
                    ]}
                />
                <Chart
                    fullWidth
                    title='Ilo rezerwacji uytkownikw'
                    data={chartsData.users}
                    xKey='email'
                    y={[
                        { label: 'Ilo rezerwacji', key: 'reservationsCount' }
                    ]}
                />
                <Chart
                    fullWidth
                    title="Czas wszystkich reserwacji danego uytkownika"
                    data={chartsData.users}
                    xKey='email'
                    y={[
                        { label: 'Czas cakowity', key: 'allTimeMinutes' },
                    ]}
                />
            </ChartSection>
        </>
    )
}