import { Card, CardHeader, CardContent, Typography, Box, CardMedia } from "@mui/material";
import ImageService from "../../../../../services/ImageService";
import LaunchIcon from '@mui/icons-material/Launch';
import Room from "../../../../../models/Room";
import React from "react";
import { useResourceMap } from "../../../../../contexts/ResourceMapContext";
import AppLink from "../../../../../shared/components/AppLink";



interface ReservationRoomCardProps {
    room: Room
}

export default function ReservationRoomCard({ room }: ReservationRoomCardProps) {
    const { getRoomLink, getBuildingLink } = useResourceMap();

    const roomLink = React.useMemo(() => getRoomLink(room.id), [room.id, getRoomLink]);
    const buildingLink = React.useMemo(() => getBuildingLink(room.building), [room.building, getBuildingLink]);

    return (
        <Card sx={{ display: 'flex', minHeight: '150px' }}>
            <CardMedia
                component='img'
                image={ImageService.getLink(room.image)}
                alt={`zdjcie sali ${room.name}`}
                sx={{
                    maxWidth: '40%',
                    maxHeight: '100%'
                }}
            />
            <Box sx={{ width: '60%' }}>
                <CardHeader
                    title={`Sala ${room.name}`}
                    subheader={room.roomType}
                    sx={{
                        pt: 1, pb: 0
                    }}
                    action={
                        <AppLink
                            to={roomLink}
                            aria-label='Otwrz stron Pokoju'
                            title='Otwrz stron Pokoju'
                        >
                            <LaunchIcon />
                        </AppLink>
                    }
                />
                <CardContent>
                    <Typography variant="body2" color="text.secondary">
                        <AppLink
                            withIcon
                            to={buildingLink}
                            aria-label="Poka budynek"
                            title="Poka budynek"
                        >
                            poka budynek
                        </AppLink> <br/>
                        Aktualnie {room.occupied ? 'zajta' : 'wolna'}<br />
                    </Typography >
                </CardContent >
            </Box >
        </Card >
    );
}